<template>
  <div class="page-register">
    <div class="header">
      <header>
        <a href="#" class="site-logo"></a>
        <div class="login">
          <span></span>
          <router-link :to="{ name: 'login' }"></router-link>
        </div>
      </header>
    </div>
    <!--  -->
    <div class="content">
      <el-form
        :model="registerForm"
        status-icon
        :rules="rules"
        ref="registerForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="userName">
          <el-input type="text" v-model="registerForm.userName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="registerForm.password"
            autocomplete="off"
            @input="input"
          ></el-input>
          <div class="pw-strength">
            <!-- bar -->
            <div :class="['bar', strengthClass]"></div>
            <div class="letter">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="" prop="rePassword">
          <el-input
            type="password"
            v-model="registerForm.rePassword"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('registerForm')"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <!-- footer -->
    <footer class="footer">
      <p>meituan.com ICP070791 11010502025545</p>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    var validateUsername = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.length < 4 || value.length > 16) {
        callback(new Error("4-16"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (value.length < 4 || value.length > 16) {
          callback(new Error("4-16"));
        }
        if (this.registerForm.rePassword !== "") {
          this.$refs.registerForm.validateField("rePassword");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.registerForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        userName: "",
        password: "",
        rePassword: "",
      },
      strengthClass: "", //
      rules: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        userName: [{ validator: validateUsername, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const result = await this.$api.register(this.registerForm);
          if (result.data.status == "success") {
            this.$router.push({name : 'login'})
          }else{
            alert(result.data.msg);
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // strengthClass
    input() {
      let password = this.registerForm.password;
      const reg = /(_)+/g; //
      const regNum = /(\d)+/g; //
      const regWord = /(\w)+/g; //
      let strong =
        password.match(reg) &&
        password.match(regNum) &&
        password.match(regWord);
      if (password.length > 20 || (password.length > 6 && strong)) {
        this.strengthClass = "strong";
      } else if (this.registerForm.password.length < 6) {
        this.strengthClass = "week";
      } else if (!this.registerForm.password) {
        this.strengthClass = "";
      } else {
        this.strengthClass = "normal";
      }
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/register/index.scss";
</style>