<template>
  <div class="page-login">
    <div class="login-header">
      <a class="logo" href="http://www.meituan.com"></a>
    </div>
    <div class="login-panel">
      <!--  -->
      <div class="banner">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt=""
        />
      </div>
      <!--  -->
      <div class="form">
        <h4 v-if="error" class="tips">{{ error }}</h4>
        <p>
          <span>  </span>
        </p>
        <el-input
          v-model="userName"
          placeholder="//"
          prefix-icon="profile"
        ></el-input>
        <el-input
          v-model="password"
          type="password"
          placeholder=""
          prefix-icon="password"
        ></el-input>
        <p class="foot">
          <a href="#">?</a>
        </p>
        <el-button type="primary" class="btn-login" @click="submit"
          ></el-button
        >
        <div class="reg">
          <span></span>
          <router-link :to="{ name: 'register' }"></router-link>
        </div>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title"></span>
          </h3>
          <div class="oauth cf">
            <a
              class="oauth__link oauth__link--qq"
              href="/account/connect/tencent"
              target="_blank"
            ></a>
            <a
              class="oauth__link oauth__link--weibo"
              href="/account/connect/sina"
              target="_blank"
            ></a>
          </div>
        </div>
      </div>
    </div>
    <!-- footer -->
    <footer>
      <ul>
        <li><a href="#"></a></li>
        <li><a href=""></a></li>
        <li><a href=""></a></li>
        <li><a href=""></a></li>
        <li><a href=""></a></li>
      </ul>
      <p>
        2018  meituan.com ICP070791 11010502025545
      </p>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: "",
      password: "",
      error: "",
    };
  },
  methods: {
    async submit() {
      if (!this.userName) {
        this.error = "";
        return false;
      }
      if (!this.password) {
        this.error = "";
        return false;
      }
      const result = await this.$api.Login({
        userName: this.userName,
        password: this.password,
      });

      const { msg, status } = result.data;
      if (status == "success") {
        // 
        this.$router.push({ name: "Index" });
        //
        this.$store.commit('setUserName',this.userName)
      } else {
        this.error = msg;
      }
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>